@import "src/styles/mixins";
@import "src/styles/constants";

* {
  box-sizing: border-box;
}

#root {
  height: 100%;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: $default-font !important;
  color: #FFFFFF;
  margin: 0;
  height: 100%;
  background: $primary-bg-blue;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.section,
.content {
  padding-inline: 80px;

  @include respond(md) {
    padding-inline: 30px;
  }

  @include respond(sm) {
    padding-inline: 20px;
  }

  &-wrapper {
    max-width: 1087px;
    width: 100%;
    margin: 0 auto;
  }

  &__title {
    color: $primary-white;
    font-size: 32px;
    font-weight: 900;
    line-height: normal;
    margin: 0;

    @include respond(md) {
      font-size: 24px;
    }

    @include respond(sm) {
      font-size: 18px;
    }
  }
}

.header {
  position: fixed;
  background: $primary-bg-black;
  width: 100%;
  height: 80px;
  transition: 0.1s;
  z-index: 10;

  @include respond(sm) {
    height: 48px;
  }

  &-container {
    height: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }

  &-links {
    display: flex;
    gap: 40px;

    &__item {
      color: #FFFFFF;
      text-decoration: none;
      cursor: pointer;
      font-size: 18px;
      font-weight: 700;
      line-height: normal;

      &:hover {
        background: linear-gradient(98deg, #EBFF00 18.56%, #FF70D9 76.02%);
        background-clip: text;
        -webkit-background-clip: text;
        -webkit-text-fill-color: transparent;
      }
    }

    &_mobile {
      padding-top: 20px;
      padding-bottom: 20px;
      display: flex;
      flex-direction: column;
      gap: 30px;
      justify-content: center;

      a {
        color: #FFFFFF;
        text-decoration: none;
        text-align: center;
      }
    }
  }

  &-sidebar {
    position: absolute;
    background-color: $primary-bg-black;
    width: 100%;
    height: 200px;
    top: -410px;
    transition: 0.5s;
    left: 0;
    opacity: 0;
    display: flex;
    justify-content: center;
    z-index: 10;

    &._active {
      top: 70px;
      opacity: 1;

      @include respond(md) {
        top: 48px;
      }
    }
  }

  &-logo {
    width: 104px;

    @include respond(md) {
      width: 71px;
    }

    img {
      width: 100%;
    }
  }

  &-burger {
    border: none;
    outline: none;
    background-color: inherit;
    padding: 0;
    width: 26px;
    margin-top: 3px;

    @include respond(md) {
      display: block;
    }
  }

  &__cart-btn {
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
  }
}

.main {
  padding-top: 80px;

  @include respond(md) {
    padding-top: 48px;
  }
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: $primary-bg-black;
  padding-bottom: 18px;

  &__text {
    color: $primary-accent-yellow;
    text-align: center;
    font-size: 18px;
    font-weight: 800;
    line-height: 28px;
    margin: 0;
  }
}

.offers {
  padding-bottom: 55px;
  padding-top: 55px;

  @include respond(md) {
    padding-bottom: 50px;
    padding-top: 50px;
  }

  &__title {
    margin-bottom: 40px;

    @include respond(md) {
      margin-bottom: 30px;
    }
  }

  &-list {
    display: grid;
    gap: 40px;
    grid-template-columns: 1fr 1fr;

    @include respond(md) {
      gap: 30px;
    }

    @include respond(sm) {
      grid-template-columns: 1fr;
    }

    &__item {
      min-height: 350px;
      border-radius: 10px;
      padding: 35px;
      background-position: center;
      background-size: cover;
      background-repeat: no-repeat;
      display: flex;
      flex-direction: column;
      justify-content: flex-end;

      @include respond(md) {
        padding: 26px;
      }

      &__title {
        font-size: 46px;
        font-weight: 800;
        line-height: 100%;
        margin: 0;
        max-width: 285px;

        @include respond(md) {
          font-size: 40px;
        }
      }

      &__desc {
        font-size: 24px;
        font-weight: 800;
        line-height: 120%;
        margin: 22px 0;

        @include respond(md) {
          font-size: 16px;
        }
      }

      &__button {
        width: 191px;
        height: 54px;
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 100px;
        border: 2px solid $primary-accent-yellow;
        font-size: 22px;
        font-weight: 700;
        line-height: normal;
        text-decoration: none;
        color: $primary-accent-yellow;
        transition: .3s;

        &:hover {
          border: 2px solid $primary-accent-yellow-hover;
          color: $primary-accent-yellow-hover;
        }

        @include respond(md) {
          width: 161px;
          height: 33px;
          font-size: 16px;
        }
      }

      &:nth-child(1),
      &:nth-child(4) {
        grid-column: 1/3;

        .offers-list__item__title {
          font-size: 74px;
          max-width: 670px;

          @include respond(md) {
            font-size: 40px;
            max-width: 285px;
          }
        }
        @include respond(md) {
          grid-column: 1;
        }
      }

      &:nth-child(4) {
        @include respond(md) {
          grid-column: 2;
        }

        @include respond(sm) {
          grid-column: 1;
        }
      }
    }
  }
}

.journey {
  padding-bottom: 60px;

  @include respond(md) {
    padding-bottom: 53px;
  }

  @include respond(sm) {
    padding-bottom: 50px;
  }

  &__text {
    margin: 17px 0;
    font-size: 20px;
    font-weight: 700;
    line-height: normal;
    opacity: 0.6;
    max-height: 150px;
    transition: .5s;
    overflow: hidden;
    display: flex;
    flex-direction: column;
    gap: 16px;

    p {
      margin: 0;
    }

    &._active {
      max-height: 500px;
    }

    @include respond(md) {
      font-size: 16px;
      margin: 20px 0;
      max-height: 115px;
    }
  }

  &__button {
    cursor: pointer;
    background: transparent;
    border: none;
    outline: none;
    color: $primary-accent-yellow;
    font-size: 20px;
    font-weight: 800;
    line-height: normal;
    padding: 0;
    transition: .3s;

    &:hover {
      color: $primary-accent-yellow-hover;
    }

    @include respond(md) {
      font-size: 16px;
    }
  }
}

.hero {
  padding-bottom: 119px;
  padding-top: 91px;
  background-size: 350px, cover, cover;
  background-position: right 70px top 70px, center, center;
  background-repeat: no-repeat, no-repeat;

  @include respond(xlg) {
    background-position: right 50px top 70px, center, center;
  }

  @include respond(lg) {
    background-position: right 0 top 70px, center, center;
  }

  @include respond(md) {
    background-position: right -90px top 50px, center, center;
    background-size: 300px, cover, cover;
    padding-bottom: 100px;
    padding-top: 100px;
  }

  @include respond(sm) {
    background-position: left 10px top -100px, center, center;
    padding-top: 250px;
    padding-bottom: 44px;
  }

  &-content {
    max-width: 650px;

    @include respond(md) {
      max-width: 400px;
    }

    @include respond(sm) {
      max-width: 100%;
    }

    &__title {
      font-size: 72px;
      font-weight: 800;
      line-height: 100%;
      margin: 0;

      @include respond(md) {
        font-size: 48px;
      }

      @include respond(sm) {
        font-size: 36px;
        text-align: center;
        max-width: 300px;
        margin: 0 auto;
      }

      span {
        color: $primary-accent-pink;
      }
    }

    &__desc {
      font-size: 32px;
      font-weight: 800;
      line-height: 120%;
      margin: 22px 0;

      @include respond(md) {
        font-size: 22px;
      }

      @include respond(sm) {
        font-size: 16px;
        margin: 17px 0;
        text-align: center;
      }

      span {
        color: $primary-accent-yellow;
      }
    }

    &__button {
      color: #000;
      font-size: 22px;
      font-weight: 700;
      line-height: normal;
      padding: 14px 40px;
      background-color: $primary-accent-yellow;
      border: none;
      cursor: pointer;
      border-radius: 100px;
      transition: .3s;

      &:hover {
        background-color: $primary-accent-yellow-hover;
      }

      @include respond(sm) {
        font-size: 16px;
        padding: 7px 40px;
        display: block;
        margin: 0 auto;
      }
    }
  }
}
